.loader {
    text-align: center;
    text-align: center;
    margin: 1rem 0;
}

.loading {
    width: 10rem;
    height: 0.7rem;
    background: linear-gradient(rgb(35 46 82), rgb(35 46 82)) center left / 100% 100% repeat-y;
    /* background-size: contain; */
    /* background-repeat: no-repeat; */
    margin: auto;
    /* border: 2px solid black; */
    border-radius: 2rem;
    border: 1px solid;
    animation: load 50s ease 0s 1 forwards;
}

.load-text {
    font-weight: bold;
}

.load-dots {
    display: inline-block;
    background-color: rgb(35 46 82);
    width: 4px;
    animation: blink 2s ease-in-out 0s infinite forwards;
    aspect-ratio: 1 / 1;
    margin: 2px;
    border-radius: 3px;
}

#dot1{animation-delay: 0s;}
#dot2{animation-delay: 0.6s;}
#dot3{animation-delay: 1.2s;}

@keyframes blink {

    0%,
    40%, 100% {
        scale: 1;
    }

    20% {
        scale: 1.5;
    }
}

@keyframes load {
    0% {
        background: linear-gradient(rgb(35 46 82), rgb(35 46 82)) center left / 0% 100% repeat-y;
    }

    100% {
        background: linear-gradient(rgb(35 46 82), rgb(35 46 82)) center left / 100% 100% repeat-y;
    }
}

.gear-box i {
    color: white;
}

.gear-box {
    display: inline-block;
    background: transparent;
    -webkit-animation: rotator 2s linear 0s infinite;
    animation: rotator 2s linear 0s infinite;
}

@-webkit-keyframes rotator {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    50% {
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes rotator {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    50% {
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

